.errorImageOverlay{height:60vh;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.errorImageOverlay .errorImageContainer{display:inline-block;background-image:url("https://i.imgur.com/oEUksmz.png");background-size:cover;background-position:center;width:50vh;height:50vh}.errorImageOverlay p{bottom:0;font-size:28px;color:#eea5b1}

body{font-family:"Roboto Condensed", "Noto Sans TC", sans-serif;overflow-x:hidden}main{width:1260px;margin:0 auto;min-height:calc(100vh - 204px)}a{text-decoration:none;color:black}*{box-sizing:border-box;margin:0;padding:0}.space{height:9rem}@media screen and (max-width: 1260px){main{width:80vw}}@media screen and (max-width: 576px){main{width:95vw}.space{height:7rem}}.next-arrow,.prev-arrow{z-index:4;position:absolute;top:40%;background:transparent;outline:transparent;border:transparent;cursor:pointer;font-size:2rem;color:#1178b8;transition:0.5s;opacity:0.7}.next-arrow:hover,.prev-arrow:hover{transform:translateY(5px);text-shadow:white 3px 4px 5px;opacity:1}.next-arrow{right:5px}.prev-arrow{left:5px}.slick-dots{bottom:0 !important}.slick-dots li button:before{color:#1178b8 !important}

